import React, { useState } from 'react'
import CalcResoult from './CalcResoult'
import './CSS/CalcStyle.css'
import Keys from './Keys'
import SecondFunctionalButton from './SecondFunctionalButton'

function CalcApp() {
    const [inpValue, setInpValue] = useState('');

    const clickKeyHandler = value => {
        const valSplit = value.replace(/ /g, '').split(',')
        if ((valSplit[0].length + (valSplit[1]?valSplit[1].length:0)) < 16) {
            const valLeft = valSplit[0]
            const valRight = (valSplit[1]!==undefined)? `,${valSplit[1]}`:''
            if (valLeft.length > 3) {
                setInpValue(beautyNumberFormat(valLeft) + valRight)
            } else {
                setInpValue(value)
            }
        }
    }

    const beautyNumberFormat = n => {
        const arrayNumber = n.split('').reverse();
        const arrayLen = arrayNumber.length
        const beautyNumber = [];
        for (let i = 0; i < arrayLen; i++) {
            beautyNumber.push(arrayNumber[i])
            if ((i + 1) % 3 === 0 && (i !== arrayLen - 1)) {
                beautyNumber.push(' ')
            }
        }

        return beautyNumber.reverse().join('')
    }

    return (
        <div className="Calc-App">
            <div className='Calc-Container'>
                <CalcResoult />
                <SecondFunctionalButton
                    inpValue={inpValue}
                    clickKeyHandler={clickKeyHandler}
                />
                <Keys
                    clickKeyHandler={clickKeyHandler}
                    inpValue={inpValue}
                />
            </div>
        </div>
    )
}

export default CalcApp