import { IconButton } from '@material-ui/core'
import { Backspace, FormatTextdirectionRToL } from '@material-ui/icons'
import React from 'react'

function SecondFunctionalButton({inpValue, clickKeyHandler}) {
    const clearBtnClick = () => {
        if(inpValue !== '') {
            clickKeyHandler(inpValue.replace(/ /g,'').slice(0,-1))
        }
    }
    return (
        <div className='second-order-keys'>
            <IconButton className='calc-adv'>
                <FormatTextdirectionRToL />
            </IconButton>
            
            <input
                className="calc-input"
                type='text'
                value={inpValue}
                onChange={() => false}
                autoFocus
            />

            <IconButton
                className='calc-backspace'
                onClick={() => clearBtnClick()}
            >
                <Backspace />
            </IconButton>
        </div>
    )
}

export default SecondFunctionalButton