import React from 'react'

const Square = (props) => {
    const style = {}

    style.background = props.value === 'X'?'green':
                                props.value === 'O'?'red':'';

    return <button 
                className="square"
                onClick={() => props.onClick()}
                style={style}
            >
            {props.value}
           </button>
}

export default Square