import React from 'react'
import Square from './Square'

class Board extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            squares: Array(9).fill(null),
            xIsNext: true
        }
    }

    handleClick(i) {
        const squares = this.state.squares.slice();

        if(!squares[i]) {
            squares[i] = this.state.xIsNext? 'X' : 'O';

            if(!calculateWinner(this.state.squares))
            this.setState({
                squares: squares,
                xIsNext: !this.state.xIsNext
            })
        }
    }

    renderSquare(i) {
        return <Square
                    value={this.state.squares[i]}
                    onClick={() => this.handleClick(i)}
                />
    }

    reset() {
        const newSquares = Array(9).fill(null);

        this.setState({
            squares: newSquares,
            xIsNext: true
        })
    }

    render() {
        const winner = calculateWinner(this.state.squares)
        const draw = drawResult(this.state.squares)

        let status = winner? `Bu o'yinda ${winner} yutdi !`
                                        :
                    `Keyingi o'yinchi: ${this.state.xIsNext? 'X':'O'}`;

        const reset = winner||draw?
                <button onClick={() => this.reset()}>
                    O'yinni qayta boshlash</button>:''

        if(!winner && draw) status = 'Kuchlar teng (DURANG)'

        return (
            <div className="board">
                <div className="status">
                    {status}
                </div>
                
                <div className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>

                <div className="reset">
                    {reset}
                </div>
            </div>
        )
    }
}

function calculateWinner(sqrs) {
    const lines = [
        [0,1,2], [3,4,5], [6,7,8],
        [0,3,6], [1,4,7], [2,5,8],
        [0,4,8], [2,4,6]
    ]

    for(let line of lines) {
        const [a, b, c] = line;

        if(sqrs[a] && sqrs[a] === sqrs[b] && sqrs[a] === sqrs[c])
            return sqrs[a];
    } return null
}

function drawResult(sqrs) {
    let k = 0
    sqrs.forEach(s => {
        if(s) k++;
    })
    return k === 9 ? 1:0
}

export default Board