import React from 'react'

function Keys({ clickKeyHandler, inpValue }) {
    const addKeyNumberValue = value => {
        if (inpValue !== '0') clickKeyHandler(inpValue + value)
        else clickKeyHandler('' + value)
    }

    const addKeyFloatValue = () => {
        if (inpValue.indexOf(',') === -1) {
            if (inpValue === '') clickKeyHandler(0 + ',')
            else clickKeyHandler(inpValue + ',')
        }
    }

    return (
        <div className="Keys">
            <div
                className='Key key-color-c'
                onClick={() => clickKeyHandler('')}
            >C</div>
            <div className='Key key-color-f'>( )</div>
            <div className='Key key-color-f'>%</div>
            <div className='Key key-color-f'></div>
            <div className='Key key-color-f'></div>

            <div className='Key' onClick={() => addKeyNumberValue(9)}>9</div>
            <div className='Key' onClick={() => addKeyNumberValue(8)}>8</div>
            <div className='Key' onClick={() => addKeyNumberValue(7)}>7</div>
            <div className='Key key-color-f'></div>
            <div className='Key key-color-f'>+</div>

            <div className='Key' onClick={() => addKeyNumberValue(6)}>6</div>
            <div className='Key' onClick={() => addKeyNumberValue(5)}>5</div>
            <div className='Key' onClick={() => addKeyNumberValue(4)}>4</div>
            <div className='Key' onClick={() => addKeyFloatValue()}>,</div>
            <div className='Key'>+/-</div>

            <div className='Key' onClick={() => addKeyNumberValue(3)}>3</div>
            <div className='Key' onClick={() => addKeyNumberValue(2)}>2</div>
            <div className='Key' onClick={() => addKeyNumberValue(1)}>1</div>
            <div className='Key' onClick={() => addKeyNumberValue(0)}>0</div>
            <div className='Key bg-equal'>=</div>
        </div>
    )
}

export default Keys